<template>
  <section>
    <header class="item-view header">
      <h5>{{ item.title }}</h5>
      <pre class="small" style="color: #999; padding: 0;">{{ item.desc }}</pre>
      <p>
        <span class="price">{{ item.price / 100 | currency "¥ " 2}}</span>
      </p>
    </header>
    
    <hr class="cell" />

    <div v-if="item.sizes && (item.sizes.length > 0)">
      <article class="flex five">
        <div class="fifth">尺寸</div>

        <div class="four-fifth">
          <span v-for="size in item.sizes" class="label">
            {{ size.label }}
          </span>
        </div>
      </article>
      <hr class="cell" />
    </div>

    <div v-if="item.colors && (item.colors.length > 0)">
      <article class="flex five">
        <div class="fifth">颜色</div>

        <div class="four-fifth">
          <span v-for="color in item.colors" class="label">
            {{ color.label }}
          </span>
        </div>
      </article>
      <hr class="cell" />
    </div>

    <article>
    </article>

    <article>
      <pre class="small center" style="color: #999;">{{ item.item_footer }}</pre>
    </article>

    <hr/>

    <p class="fancy subtitle"><span class=" small mute">下拉查看图文详情</span></p>
    <p><!-- end, to skip past :last-child style --></p>

    <article class="item-view detail-photos">
      <div v-for="photo in item.detail_photos" class="full-width">
        <img :src="photo.image_url" /></div>
      </div>
    </article>
    

  </section>  
</template>

<script>

export default {
  name: 'ItemViewBody',
  props: {
    item: Object
  },
}

</script>


<style>

    /* fancy before/after horizontal line decoration*/
    .subtitle {
      margin: 2em 0;
    }
    .fancy {
      line-height: 0.5;
      text-align: center;
    }
    .fancy span {
      display: inline-block;
      position: relative;  
    }
    .fancy span:before,
    .fancy span:after {
      content: "";
      position: absolute;
      height: 5px;
      /*border-bottom: 1px solid #DDD;*/
      border-top: 1px solid #DDD;
      top: 2.5px;
      /*width: 600px;*/
      width: 50%;
    }
    .fancy span:before {
      right: 100%;
      margin-right: 15px;
    }
    .fancy span:after {
      left: 100%;
      margin-left: 15px;
    }

</style>



